	module.exports = {"data":{"wordpressPost":{"title":"Antidepressants and risk of mania, medically unexplained symptoms and the association between alcohol and mortality in Alzheimer&#8217;s: Most read articles in December","content":"<h1>Antidepressants, medically unexplained symptoms, and alcohol consumption in Alzheimer&#8217;s patients</h1>\n<p><img class=\"alignnone\" title=\"Image: Glass_of_white_wine.jpg, from Wikipedia, by Jon Sullivan\" src=\"https://upload.wikimedia.org/wikipedia/commons/thumb/2/29/Glass_of_white_wine.jpg/800px-Glass_of_white_wine.jpg\" alt=\"File:Glass of white wine.jpg\" width=\"303\" height=\"227\" /></p>\n<p>December saw a lot of new papers entering the Top 10 Most Read, with half of them being from the current issue. Patel <em>et al</em>&#8216;s research paper, complete with video abstract and press release, questions whether antidepressants increase the risk of mania and bipolar disorder in people with depression in the top spot this month. Miller <em>et al</em> and Kristensen <em>et al</em> maintain their places at numbers two and three, with papers on clinical trial registration and the effect of statins on survival. Our fourth most read article examines junior doctors&#8217; knowledge and experiences of managing patients with medically unexplained symptoms via a qualitative study using in-depth interviews. Berntsen <em>et al</em> enter the list at number seven, with a prospective cohort study investigating the association between alcohol consumption and mortality in patients with Alzheimer&#8217;s disease, and Jonas <em>et al</em> write about surgery and the placebo response.</p>\n<table border=\"1\" width=\"483\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Patel <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/12/e008341.full\">Do antidepressants increase the risk of mania and bipolar disorder in people with depression? A retrospective electronic case register cohort study</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Miller <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/11/e009758.full\">Clinical trial registration, reporting, publication and FDAAA compliance: a cross-sectional analysis and ranking of new drugs approved by the FDA in 2012</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Kristensen <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/9/e007118.full\">The effect of statins on average survival in randomised trials, an analysis of end point postponement</a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Yon <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/12/e009593.full\">Junior doctors&#8217; experiences of managing patients with medically unexplained symptoms: a qualitative study</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Martin-Misener <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/6/e007167.full\">Cost-effectiveness of nurse practitioners in primary and specialised ambulatory care: systematic review</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Bourne <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/1/e006687.full\">The impact of complaints procedures on the welfare, health and clinical practise of 7926 doctors in the UK: a cross-sectional survey</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Berntsen <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/12/e007851.full\">Alcohol consumption and mortality in patients with mild Alzheimer&#8217;s disease: a prospective cohort study</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Jonas <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/12/e009655.full\">To what extent are surgery and invasive procedures effective beyond a placebo response? A systematic review with meta-analysis of randomised, sham controlled trials</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Boaz <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/12/e009415.full\">Does the engagement of clinicians and organisations in research improve healthcare performance: a three-stage review</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Hysing <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/1/e006748.full\">Sleep and use of electronic devices in adolescence: results from a large population-based study</a></td>\n</tr>\n</tbody>\n</table>\n<p><em>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.</em><!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"880ec165-d53c-542b-965f-62d7fbdb1aba"}}

